				parkId: uni.getStorageSync('parkId'),
				pageNum: '1',
				pageSize: '99'
			},
			method: 'GET',
			success: res => {
				console.log(res);
				if (res.statusCode !== 200) {
					uni.reLaunch({
						url: '../login/login'
					});
				}
				this.categoryList = res.data.content;
			}
		});



		//开启定时器
		this.Timer();
		//加载活动专区
		this.loadPromotion();
	},
